<template>
    <div style="width:24rem; margin:0 auto;">
        <vue-headful title="Panel - Water BH"></vue-headful>
        <div style="position:static;">
            <img src="../assets/logo.jpg" alt="Logo Federación" srcset="" id="Logo" style="width:80%;">
        </div>
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <v-card style="position: static;">
            <v-card-title>
                INICIAR SESIÓN
            </v-card-title>
            <v-form v-on:submit.prevent="logIn" style="text-align:center;">
                <v-container>
                    <b-form-group style="padding-bottom:4em;">

                        <v-text-field required
                                      v-model="user.username"
                                      name="username"
                                      label="Nombre de usuario"
                                      placeholder="">
                        </v-text-field>
                        <v-text-field required
                                      v-model="user.password"
                                      name="password"
                                      label="Contraseña"
                                      placeholder="mi contraseña"
                                      :type="show ? 'text' : 'password'"
                                      :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'"
                                      @click:append="show = !show">
                        </v-text-field>
                    </b-form-group>
                    <v-btn class="mr-4" @click="controlaruser()"> Iniciar Sesión </v-btn>
                </v-container>
            </v-form>
        </v-card>
    </div>
</template>

<script>
    import swal from "sweetalert";
    import { APIControler } from "@/store/APIControler";
    import Vue from 'vue';
    import VueSession from 'vue-session'
    Vue.use(VueSession)

    export default {
        data() {
            return {
                show: false,
                user: {
                    username: "",
                    password: "",
                },
                listrepartos: [],
                op_repartos: [],
                stocks: [],
                listusers: [],
                op_stocks:[],
            };
        },
        methods: {
            async getUsuarios() {
                let productosAPI = new APIControler();
                productosAPI.apiUrl.pathname = "usuarios/";
                this.listusers = await productosAPI.getData();
            },
            fechaactual() {
                const f = new Date();
                //Devuelve formato sólo de fecha pero en el formato regional actual ejemplo: 24/8/2019
                console.log(f.toLocaleDateString());

                /**
                 * Si no le convence, puede recurrir a hacer el ajuste manual
                 **/
                const formatDate = (d) => {
                    return d.getFullYear() + "-" + ('0' + (d.getMonth() + 1)).slice(-2) + "-" + ('0' + d.getDate()).slice(-2)
                }
                console.log(formatDate(f));
                return (formatDate(f));
            },
            controlaruser()
            {
                let usuarious = String(this.user.username);
                let contraus = String(this.user.password);
                console.log(usuarious + contraus);
                this.listrepartos.forEach(s => {
                    if ((s.usuario == usuarious) && (s.contrasenia == contraus)) {
                        if (s.cuentaactiva) {
                            window.location.replace('/');
                        } else
                        {
                            swal("Su cuenta esta desactivada", " ", "error");
                        }
                    }
                });
                this.listusers.forEach(s => {
                    console.log(s.usuar);
                    console.log('contrasenia:' + s.passw)
                    if ((s.usuar == usuarious) && (s.passw == contraus))
                    {
                        this.$session.start()
                        this.$session.set('tipocuenta', s.tipocuenta);
                        window.location.replace('/');
                    }
                });
                setTimeout(() => {
                    swal("Usuario o contraseña Incorrectos", " ", "error");
                }, 1000);
            },
            async logIn() {
                await this.fetcher.login(this.user)
                location.reload()
            }
        },
        beforeMount() {
            this.getUsuarios();
        },
    };
</script>

